/**
 * External Dependencies
 */
import { z } from "zod";
/**
 * Internal Dependencies
 */
import { restaurantInfoSchema, scheduleManagementSchema, pickupSchema, deliverySchema, reservationSchema, trueFalseField, } from "@/admin/features/settings/settingsSchema";
import { onboardSchema } from "@/admin/features/onboard/onboardSchema";
/**
 * Location-specific fields schema
 */
const locationSpecificSchema = z.object({
    // Location-specific required fields
    location: onboardSchema.shape.restaurant_location,
    status: z.enum(["publish", "draft"]),
    // Location-specific override toggle fields
    override_restaurant_schedule: trueFalseField.optional(),
    override_online_ordering: trueFalseField.optional(),
    override_reservation: trueFalseField.optional(),
    // Location-specific module enable/disable fields
    enable_pickup: trueFalseField.optional(),
    enable_delivery: trueFalseField.optional(),
    enable_reservation: trueFalseField.optional(),
    reservation_enable_table_layout: trueFalseField.optional(),
});
/**
 * Location Create/Edit Form Schema
 * Selectively picks needed schemas from settings instead of extending all
 */
export const locationFormSchema = restaurantInfoSchema
    .merge(scheduleManagementSchema)
    .merge(pickupSchema)
    .merge(deliverySchema)
    .merge(reservationSchema)
    .merge(locationSpecificSchema)
    .partial(); // All fields are optional except location-specific required ones
/**
 * Utility function to extract location-relevant fields from settings data
 * This ensures we only get the fields that are actually used in location forms
 */
export const getLocationDefaultsFromSettings = (settingsData) => {
    if (!settingsData)
        return {};
    // Get the shape of locationFormSchema to know which fields to pick
    const locationSchemaKeys = Object.keys(locationFormSchema.shape);
    // Pick only the fields that exist in both settingsData and locationFormSchema
    const locationDefaults = {};
    locationSchemaKeys.forEach((key) => {
        if (settingsData[key] !== undefined) {
            locationDefaults[key] = settingsData[key];
        }
    });
    return locationDefaults;
};
