/**
 * Wordpress Dependencies
 */
import { settingsSchema } from "@/admin/features/settings/settingsSchema";
import { __ } from "@wordpress/i18n";
/**
 * External Dependencies
 */
import { z } from "zod";
export const locationValueSchema = z.object({
    address: z.string(),
    lat: z.number(),
    lng: z.number(),
});
// Now selectively require certain fields
export const onboardSchema = settingsSchema.extend({
    restaurant_type: z.preprocess((val) => (val === undefined ? [] : val), z.array(z.enum(["food_ordering", "reservation"])).min(1, {
        message: __("Select at least one restaurant type", "wpcafe"),
    })),
    restaurant_name: z
        .string()
        .trim()
        .min(1, __("Restaurant name is required", "wpcafe")),
    restaurant_location: z
        .union([
        z
            .string()
            .trim()
            .min(1, __("Restaurant location is required", "wpcafe")),
        locationValueSchema,
        z.null(),
    ])
        .refine((val) => {
        if (!val)
            return false;
        if (typeof val === "string")
            return val.trim().length > 0;
        if (typeof val === "object" && val.address)
            return val.address.trim().length > 0;
        return false;
    }, {
        message: __("Restaurant location is required", "wpcafe"),
    }),
    terms_agreed: z.boolean().optional(), // Optional checkbox for updates
});
