/**
 * WordPress Dependencies
 */
import { useSelect } from "@wordpress/data";
import { useEffect } from "@wordpress/element";
/**
 * External Dependencies
 */
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
/**
 * Internal Dependencies
 */
import { locationFormSchema, getLocationDefaultsFromSettings, } from "../create-and-update/schema";
import useLocationApi from "./useLocationApi";
import { stores } from "@/globalConstant";
/**
 * Custom hook to manage location form state and operations
 * Handles both create and edit modes with proper data loading and form initialization
 */
export const useLocationForm = ({ locationId }) => {
    const { getSingleLocation } = useLocationApi();
    // Access states from stores
    const { data: settingsData } = useSelect((select) => select(stores === null || stores === void 0 ? void 0 : stores.settings).getSettingsState(), []);
    const { singleLocation } = useSelect((select) => select(stores === null || stores === void 0 ? void 0 : stores.location).getLocationState(), []);
    // Get only location-relevant fields from settings data
    const settingsDefaults = getLocationDefaultsFromSettings(settingsData);
    const defaultValues = Object.assign(Object.assign({}, settingsDefaults), { 
        // Location-specific defaults
        restaurant_name: "", location: "", status: "publish", override_restaurant_schedule: "0", override_online_ordering: "0", override_reservation: "0", override_pickup_schedule: "0", override_delivery_schedule: "0", override_reservation_schedule: "0", 
        // Location-specific module enable/disable defaults
        enable_pickup: "0", enable_delivery: "0", enable_reservation: "0", reservation_enable_table_layout: "0" });
    // Map location data to form values
    const mapLocationToFormValues = (location) => {
        // Start with default values and override with location-specific values
        const mappedValues = Object.assign({}, defaultValues);
        // Override with location data where available
        Object.keys(defaultValues).forEach((key) => {
            const locationValue = location === null || location === void 0 ? void 0 : location[key];
            if (locationValue !== undefined && locationValue !== null) {
                mappedValues[key] =
                    locationValue;
            }
        });
        // Ensure required location-specific fields have proper defaults
        mappedValues.restaurant_name =
            (location === null || location === void 0 ? void 0 : location.restaurant_name) || defaultValues.restaurant_name;
        mappedValues.location = (location === null || location === void 0 ? void 0 : location.location) || defaultValues.location;
        mappedValues.status = (location === null || location === void 0 ? void 0 : location.status) || defaultValues.status;
        // Ensure override fields have proper string defaults
        mappedValues.override_restaurant_schedule =
            (location === null || location === void 0 ? void 0 : location.override_restaurant_schedule) ||
                defaultValues.override_restaurant_schedule;
        mappedValues.override_online_ordering =
            (location === null || location === void 0 ? void 0 : location.override_online_ordering) ||
                defaultValues.override_online_ordering;
        mappedValues.override_reservation =
            (location === null || location === void 0 ? void 0 : location.override_reservation) ||
                defaultValues.override_reservation;
        mappedValues.override_pickup_schedule =
            (location === null || location === void 0 ? void 0 : location.override_pickup_schedule) ||
                defaultValues.override_pickup_schedule;
        mappedValues.override_delivery_schedule =
            (location === null || location === void 0 ? void 0 : location.override_delivery_schedule) ||
                defaultValues.override_delivery_schedule;
        mappedValues.override_reservation_schedule =
            (location === null || location === void 0 ? void 0 : location.override_reservation_schedule) ||
                defaultValues.override_reservation_schedule;
        // Ensure location-specific module enable/disable fields have proper defaults
        mappedValues.enable_pickup =
            (location === null || location === void 0 ? void 0 : location.enable_pickup) || defaultValues.enable_pickup;
        mappedValues.enable_delivery =
            (location === null || location === void 0 ? void 0 : location.enable_delivery) || defaultValues.enable_delivery;
        mappedValues.enable_reservation =
            (location === null || location === void 0 ? void 0 : location.enable_reservation) || defaultValues.enable_reservation;
        mappedValues.reservation_enable_table_layout =
            (location === null || location === void 0 ? void 0 : location.reservation_enable_table_layout) ||
                defaultValues.reservation_enable_table_layout;
        return mappedValues;
    };
    // Initialize form with proper configuration
    const methods = useForm(Object.assign({ resolver: zodResolver(locationFormSchema), 
        // Fallback default values for create mode
        defaultValues: defaultValues }, (locationId &&
        singleLocation && {
        values: mapLocationToFormValues(singleLocation),
    })));
    // Fetch location data for edit mode
    useEffect(() => {
        if (!locationId)
            return;
        if (singleLocation && singleLocation.term_id === locationId)
            return; // Check if we have the correct location data
        getSingleLocation(locationId);
    }, [locationId]);
    // Determine mode
    const isEditMode = Boolean(locationId);
    const isLoading = isEditMode &&
        (!singleLocation || singleLocation.term_id !== locationId);
    return {
        // Form methods
        methods,
        // Convenience destructured methods
        handleSubmit: methods.handleSubmit,
        reset: methods.reset,
        // State
        isEditMode,
        isLoading,
        locationId,
        // Data
        settingsData,
        singleLocation,
    };
};
